import { ResizeObserver as polyfill } from '@juggle/resize-observer';

import { isSSR } from '../index';

/** Полифилл для браузеров, не поддерживающих ResizeObserver API @see https://caniuse.com/resizeobserver
 * для установки импортировать первым во входную точку приложения.
 * */
if (!isSSR) {
  window.ResizeObserver = window.ResizeObserver || polyfill;
}
